body {
  margin: 0;
  font-family: 'Segoe UI', sans-serif;
}

.landing-container {
  background: linear-gradient(to right, #1c1c1c, #292929);
  color: #fff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Navbar */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 3rem;
  background-color: rgba(0, 0, 0, 0.9);
  border-bottom: 1px solid #444;
}

.logo {
  display: flex;
  align-items: center;
}

.logo img {
  width: 40px;
  margin-right: 10px;
}

.logo span {
  font-size: 1.5rem;
  font-weight: bold;
}

.auth-button .btn {
  padding: 0.6rem 1.2rem;
  background-color: #ff4757;
  color: white;
  border-radius: 5px;
  text-decoration: none;
  font-weight: 600;
  transition: background-color 0.3s;
}

.auth-button .btn:hover {
  background-color: #ff6b81;
}

/* Hero Section */
.hero {
  text-align: center;
  padding: 6rem 2rem 4rem;
  background: url('/background.jpg') no-repeat center center/cover;
  background-blend-mode: overlay;
  background-color: rgba(0, 0, 0, 0.7);
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero-text h1 {
  font-size: 3.5rem;
  margin-bottom: 1rem;
}

.hero-text p {
  font-size: 2rem;
  color: #ddd;
  font-style: italic;
  max-width: 800px;
  margin: 0 auto;
}

/* Movie Section */
.movie-section {
  text-align: center;
  padding: 3rem 2rem;
  background-color: #111;
}

.movie-section h2 {
  margin-bottom: 2rem;
  font-size: 2rem;
}

.movie-posters {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1rem;
}

.movie-posters img {
  width: 150px;
  height: 225px;
  object-fit: cover;
  border-radius: 10px;
  transition: transform 0.3s;
}

.movie-posters img:hover {
  transform: scale(1.05);
}
